.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 50%;
  z-index: 1;
}


.slides {
  position: relative;
  background: linear-gradient( rgba(0, 0, 0, 0.685), rgba(0, 0, 0, 0.685) ),url('https://alumni.cit.ac.in/assets/images/FooterCit.jpeg');
  background-size: cover;
  background-blend-mode:darken;
}

.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 50%;
  z-index:1;
}

.slides .slick-prev {
  left: 5%;
}

.slides .slick-next {
  right: 5%;
}



